.breadcrumbs {
  width: 100%;
  padding: 20px 0 30px;

  /*@include vp-1023 {
    padding: 30px 0 40px;
  }

  @include vp-767 {
    padding: 20px 0 40px;
  }*/

  &--absolute {
    z-index: 1;
    position: absolute;

    top: 0;
    left: 0;
  }

  &__list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  &__item {
    display: flex;
    align-items: center;

    svg {
      flex-shrink: 0;

      width: 10px;
      height: 10px;
      margin: 0 7px;

      transform: rotate(-90deg);

      color: black;
    }

    &:last-child {
      
      svg {
        display: none;
      }
    }
  }

  &__item:last-child > &__link {
    color: rgb(21, 39, 127);
  }

  &__link {
    font-family: Blinker, sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 130%;
    text-transform: uppercase;

    color: black;

    transition: color $trans-default;

    @include hover-focus {
      color: rgb(29, 54, 175);
    }

    @include active {
      color: rgb(21, 39, 127);
    }

    &.is-active {
      color: rgb(29, 54, 175);

      pointer-events: none;
    }
  }
}
