.intro {
  position: relative;
  //overflow: hidden;
  
  &__background {
    z-index: -1;
    position: absolute;

    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 98%;

      object-fit: cover;
    }
  }

  &__wrapper {
    //position: relative;
    padding: 88px 0 0;

    /*@include vp-1023 {
      padding: 60px 0 215px;
    }*/
    @media screen and (max-width: 920px) {
      padding: 60px 0 0;
    }

    /*@include vp-767 {
      padding: 70px 0;
    }*/
  }

  &__title {
    max-width: 900px;
    margin: 0 0 60px;

    font-family: $ff-blinker;
    font-weight: 700;
    font-size: 72px;
    line-height: 130%;
    text-transform: uppercase;

    color: $color-default-white;

    /*@include vp-1023 {
      max-width: 680px;

      font-size: 54px;
      line-height: 65px;
    }*/
    @media screen and (max-width: 1280px) {
      padding-bottom: 80px;
      margin-bottom: 0;
    }
   
    @media screen and (max-width: 920px) {
      font-size: 52px;
      line-height: 120%;

    }

    @media screen and (max-width: 650px) {
      font-size: 32px;
    }

    /*@include vp-767 {
      //max-width: 330px;
      margin-bottom: 330px;
    }*/

    @include vp-389 {
      font-size: 32px;
      padding-bottom: 0px;
    }
  }

  /*&__description {
    max-width: 393px;
    margin: 0 0 40px;

    font-weight: 500;
    font-size: 24px;
    line-height: 34px;

    color: $color-default-white;

    @include vp-1023 {
      max-width: 300px;

      font-size: 18px;
      line-height: 25px;
    }

    @include vp-767 {
      max-width: 320px;
      margin: 0 0 20px;

      font-size: 16px;
      line-height: 22px;
    }
  }*/
  &__down {
    display: flex;
    //flex-direction: reverse;
    align-items: center;
    //justify-content: flex-start;
    gap: 0 290px;
    height: 0;
    padding-bottom: 270px;

    @media screen and (max-width: 1280px) {
      gap: 0 40px;
      
    }

    @media screen and (max-width: 950px) {
      padding-bottom: 180px;
    }

    @media screen and (max-width: 768px) {
      padding-bottom: 130px;
    }

    @media screen and (max-width: 650px) {
      flex-direction: column-reverse;
      align-items: start;
      padding-bottom: 50px;
      padding-top: 300px;
    }
    
  }

  &__btn {
    border-radius: 2px;
    background: rgb(255, 255, 255);
    max-width: 300px;
    width: 100%;
    display: block;
    color: rgb(24, 44, 143);
    font-family: Blinker, sans-serif;
    font-size: 16px;
    font-weight: 600;
    line-height: 140%;
    letter-spacing: 2%;
    text-align: center;
    text-transform: uppercase;
    padding: 17px;
    
  }

  &__image {
    
    

    img {
      
      z-index: 1;
      //top: 40px;
      //right: 260px;
      //left: calc(100% - 950px + 38px);
      overflow: visible;
      //width: 780px;
      height: auto;
    }

    @media screen and (max-width: 650px) {
      transform: translateX(-100px);
      width: 420px;
    }

    @media screen and (max-width: 389px) {
      transform: translateX(-60px);
    }
  
      
    

  /*@media screen and (max-width: 920px) {
    img {
      margin-top: 70px;
      //width: 100%;
    }
  }*/
    
    /*@include vp-1023 {
      img {
        
        left: 0;
        width: 420px;
        height: 400px;
      }
    }*/

    /*@include vp-767 {
      //position: relative;
      img {
        width: 620px;
        height: 600px;
        max-width: 100%;
      }
      
    }*/

    @media screen and (max-width: 551px) {
      img {
        top: 270px;
      }
    }

    @media screen and (max-width: 436px) {
      img {
        top: 330px;
      }
    }

    @include vp-389 {
      width: 100%;
      img {
        top: 150px;
        
      }
    }

    img {
      object-fit: contain;
    
    }
  }

