.sertificates {
    background: rgb(245, 245, 245);
    &__content {
        max-width: 1420px;
        width: 100%;
        margin: 0 auto;
        padding: 70px 10px;
    }

    &__title {
        color: rgb(26, 26, 26);
        font-family: Blinker, sans-serif;
        font-size: 45px;
        font-weight: 700;
        line-height: 130%;
        text-transform: uppercase;
        margin: 0 0 40px;
    }

    &__list {
        display: flex;
        gap: 20px;
        align-items: center;
        justify-content: space-between;
    }
    .lightbox {
        position: fixed;
        overflow: hidden;
        z-index: 9999;
        width: 0;
        height: 0;
        text-align: center;
        top: 0;
        left: 0;
        background: rgba(0,0,0,0.8);
    }
    .lightbox img {
        width: 100%;
        max-width: 500px;
        height: auto;
        margin: 20px auto;
        opacity: 0;
        border: 1px solid #4D4D4D;
        -moz-box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8);
        -webkit-box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8);
        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8);
        -webkit-transition: opacity 500ms ease-in;
        -moz-transition: opacity 500ms ease-in;
        transition: opacity 500ms ease-in;  
    }
    .lightbox:target {
        width: auto;
        height: auto;
        bottom: 0;
        right: 0;
        outline: none;
    }
    .lightbox:target img {
        opacity: 1;
    }

    @media screen and (max-width: 768px) {
        &__content {
            padding: 70px 45px;
        }

        &__title {
            font-size: 32px;
            margin-bottom: 30px;
        }

        &__list {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 40px;
        }
    }

    @media screen and (max-width: 390px) {
        &__content {
            padding: 50px 35px;
        }

        &__title {
            font-size: 24px;
        }

        &__list {
            grid-template-columns: 1fr;
            gap: 30px;
        }
    }
}