.footer {
  background-color: rgb(250, 250, 253);
  &__content {
    max-width: 1420px;
    width: 100%;
    margin: 0 auto;
    padding: 0 40px;
  }

  &__logo {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;

    width: 290px;
    height: 70px;

    @include vp-1023 {
      width: 190px;
    }

    img {
      width: 290px;
      height: 70px;

      object-fit: contain;

      @include vp-1023 {
        width: 190px;
      }
    }
  }

  &__links {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 70px;
    border-bottom:  0.3px solid rgb(128, 128, 128);
    padding-bottom: 20px;
  }

  &__social {
    display: inline-flex;
    gap: 0 10px;
  }

  &__social-link:hover {
    svg {
      path {
        filter: brightness(1.5);
      }
    }

  }

  &__info {
    position: relative;
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 70px;
  }

  &__adress {
    max-width: 316px;
    width: 100%;
  }

  &__name {
    color: rgb(24, 44, 143);
    font-family: Blinker, sans-serif;
    font-size: 18px;
    font-weight: 700;
    margin: 0 0 10px 0;
  }

  &__adress-place {
    color: rgb(26, 26, 26);
    font-family: Blinker, sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 140%;
    margin: 0;
  }

  &__adress-protect,
  &__protect-link {
    color: rgb(128, 128, 128);
    font-family: Inter, sans-serif;
    font-size: 16px;
    line-height: 140%;
    margin-top: 70px;
  }


  &__protect-link {
    margin: 0;
    display: inline;
    text-decoration: underline;
    transition: .3s;
  }

  &__protect-link:hover {
    color: rgb(29, 54, 175);
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__contacts-mail,
  &__contacts-phone {
    color: rgb(26, 26, 26);
    font-family: Blinker, sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 140%;
  }

  &__category {
    display: flex;
    flex-direction: column;
  }

  &__link-category {
    color: rgb(26, 26, 26);
    font-family: Blinker, sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 140%;
    margin-bottom: 20px;
    transition: .3s;
  }

  &__link-category:hover {
    color: rgb(29, 54, 175);
  }

  &__menu {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__basic-link:hover {
    color: rgb(29, 54, 175);
  }

  &__policy {
    text-align: right;
  }

  &__policy-text,
  &__autor {
    color: rgb(128, 128, 128);
    font-family: Blinker, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
    transition: .3s;
  }

  &__policy-text {
    max-width: 179px;
    width: 100%;
    margin: 15px 0 30px;
  }

  &__autor {
    max-width: 158px;
    width: 100%;
    display: inline-block;
    text-decoration: underline;
  }

  &__policy-text:hover,
  &__autor:hover {
    color: rgb(29, 54, 175);
  }

  &__social-mobile {
    display: none;
  }

  @media screen and (max-width: 1200px) {
    &__info {
      flex-wrap: wrap;
    }

    &__policy-text,
    &__autor {
      max-width: 100%;
      text-align: left;
    }
  }

  @media screen and (max-width: 905px) {
    &__info {
      display: grid;
      grid-template-columns: 1.5fr 1fr;
      gap: 20px;
    }

    &__adress {
      grid-column: 2;
    }

    &__contacts {
      grid-column: 2;
      margin-top: -100px;
      gap: 25px;
    }

    &__policy {
      grid-column: 2;
      margin-top: -80px;
      margin-bottom: 70px;
    }
    
    &__menu {
      grid-column: 1;
      order: -1;
      gap: 15px;
      margin-bottom: 25px;
    }

    &__category {
      grid-row: 2;
    }

    &__adress-protect {
      position: absolute;
      bottom: 15px;
      left: 0px;
      max-width: 320px;
      width: 100%;
      margin: 0;
    }
  }

  @media screen and (max-width: 768px) {
    &__content {
      padding: 0 45px;
    }

    &__links {
      margin-top: 60px;
    }
  }

  @media screen and (max-width: 650px) {
    &__content {
      padding: 0 35px;
    }
    
    &__links {
      border-bottom: none;
      padding-bottom: 28px;
    }

    &__social {
      display: none;
    }

    &__adress-protect {
      display: none;
    }

    &__info {
      display: flex;
      flex-direction: column;
      margin-bottom: 50px;
    }

    &__menu {
      order: 1;
      margin-top: 10px;
    }

    &__social-mobile {
      display: block;
      
    }

    &__social-links {
      display: flex;
      gap: 0 10px;
    }

    &__adress-protect-mobile {
      margin: 20px 0 0 0;
      color: rgb(128, 128, 128);
      font-family: Inter, sans-serif;
      font-size: 16px;
      border-bottom: 0.3px solid rgb(24, 44, 143);
      padding-bottom: 30px;
      width: 100%;
    }

    &__protect-link-mobile {
      color: rgb(128, 128, 128);
      font-family: Inter, sans-serif;
      font-size: 16px;
      line-height: 140%;
      display: inline;
      text-decoration: underline;
      transition: .3s;
    }

    &__protect-link-mobile:hover {
      color: rgb(29, 54, 175);
    }

    &__contacts {
      margin-top: 0;
    }

    &__policy {
      margin: 0;
      order: 5;
    }

    &__category {
      order: 2;
    }
  }

  @media screen and (max-width: 390px) {
    &__content {
      padding: 0 35px;
    }

    &__links {
      margin-top: 50px;
      padding-bottom: 0;
    }
  }

  
}
