.container {
  width: 100%;
  max-width: 1420px;
  margin: 0 auto;
  padding: 0 40px;
}
.header {
  width: 100%;
  background-color: #fff;
}
.header.is-open .main-nav__toggle span:nth-child(1) {
  top: 24px;
  -webkit-transform: rotate(-47deg) translate(-1px, -50%) !important;
  -ms-transform: rotate(-45deg) translate(0, -50%);
  transform: rotate(-45deg) translate(0, -50%);
}
.header.is-open .main-nav__toggle span:nth-child(2) {
  opacity: 0;
  visibility: hidden;
}
.header.is-open .main-nav__toggle span:nth-child(3) {
  top: 14px !important;
  -webkit-transform: rotate(45deg) translate(0, 50%);
  -ms-transform: rotate(45deg) translate(0, 50%);
  transform: rotate(45deg) translate(0, 50%);
}
.header.is-open .main-nav__menu {
  -webkit-transform: translateX(0);
  -ms-transform: translateX(0);
  transform: translateX(0);
}
.header__logo,
.header__wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.header__logo {
  position: relative;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  width: 290px;
  height: 80px;
}
.header__logo img {
  width: 290px;
  height: 80px;
  -o-object-fit: contain;
  object-fit: contain;
}
.main-nav {
  width: 100%;
  margin: 0 0 0 150px;
}
.main-nav__toggle {
  position: relative;
  display: none;
  width: 37px;
  height: 25px;
  padding: 10px;
  //border-radius: 6px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-transition: opacity #182c8f;
  -o-transition: opacity #182c8f;
  transition: opacity #182c8f;
}
.main-nav__toggle span {
  display: block;
  position: absolute;
  left: 10px;
  width: calc(100% - 20px);
  height: 3.5px;
  border-radius: 0 !important;
  background-color: #182c8f;
  -webkit-transition: opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
  -o-transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
}
.main-nav__toggle span:nth-child(1) {
  top: 15px !important;
}
.main-nav__toggle span:nth-child(2) {
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.main-nav__toggle span:nth-child(3) {
  bottom: 15px !important;
}
.main-nav__toggle.focus-visible:focus {
  opacity: 0.8;
}
.main-nav__list,
.main-nav__menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.main-nav__menu {
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-column-gap: 20px;
  -moz-column-gap: 20px;
  column-gap: 20px;
  width: 100%;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__item {
  position: relative;
  margin: 0 70px 0 0;
}
.main-nav__item:last-child,
.main-nav__subitem:last-child {
  margin: 0;
}
.main-nav__link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  font-weight: 600;
  font-size: 18px;
  line-height: 100%;
  color: #1a1a1a;
  text-transform: uppercase;
  -webkit-transition: color 0.3s ease;
  -o-transition: color 0.3s ease;
  transition: color 0.3s ease;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.main-nav__link::after {
  content: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 10px;
}
.main-nav__link svg {
  -ms-flex-negative: 0;
  flex-shrink: 0;
  width: 16px;
  height: 16px;
  margin: 0 0 0 4px;
  color: #000;
  -webkit-transition: color 0.3s ease, -webkit-transform 0.3s ease;
  -o-transition: color 0.3s ease, transform 0.3s ease;
  transition: color 0.3s ease, transform 0.3s ease;
  transition: color 0.3s ease, transform 0.3s ease, -webkit-transform 0.3s ease;
}
.main-nav__link.focus-visible:focus,
.main-nav__link.focus-visible:focus svg,
.main-nav__sublink.focus-visible:focus {
  color: #182c8f;
}
.main-nav__sublist {
  z-index: 10;
  position: absolute;
  top: calc(100% + 10px);
  left: -30px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  min-width: 317px;
  margin: 0;
  padding: 20px 30px 30px;
  list-style: none;
  border-radius: 0 0 6px 6px;
  background-color: #fff;
  -webkit-box-shadow: 0 4px 5px 0 rgba(80, 80, 80, 0.1);
  box-shadow: 0 4px 5px 0 rgba(80, 80, 80, 0.1);
  -webkit-transition: opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
  -o-transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
  transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s ease;
  opacity: 0;
  visibility: hidden;
}
.main-nav__subitem {
  margin: 10px 0px 30px;
}
.main-nav__sublink {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: #1a1a1a;
  font-family: Blinker, sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 130%;
  letter-spacing: 2%;
  text-align: left;
  text-transform: uppercase;
  -webkit-transition: color 0.3s ease;
  -o-transition: color 0.3s ease;
  transition: color 0.3s ease;
}

.main-nav__sublist1 {
  display: none;
}

.main-nav__subitem1 {
  margin-top: 20px;
}

.main-nav__sublink1 {
  color: rgb(26, 26, 26);
  font-family: Blinker;
  font-size: 18px;
  font-weight: 600;
  line-height: 130%;
  display: block;
  width: 100%;
  
}

.main-nav__sublist li:hover > .main-nav__sublist1 {
  display: block;
}

.main-nav__sublist li {
  position: relative;
}

.main-nav__sublink1:hover {
  color: rgb(24, 44, 143);
}

.main-nav__sublink_active {
  background-color: #182c8f !important;
}
.main-nav__contacts {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  gap: 20px 0;
}
.main-nav__phone {
  color: #172a87;
  font-size: 16px;
  font-weight: 600;
  line-height: 100%;
  -webkit-transition: color 0.3s ease;
  -o-transition: color 0.3s ease;
  transition: color 0.3s ease;
}
.btn.focus-visible:focus,
.main-nav__email.focus-visible:focus,
.main-nav__phone.focus-visible:focus {
  color: #1d36af;
}
.main-nav__email {
  color: #172a87;
  font-family: Blinker, sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 100%;
  -webkit-transition: color 0.3s ease;
  -o-transition: color 0.3s ease;
  transition: color 0.3s ease;
}

@media (min-width: 1023px) {
  .main-nav__item.menu-item-has-children:has(.focus-visible) .main-nav__link {
      color: #182c8f;
  }
  .main-nav__item.menu-item-has-children:has(.focus-visible) .main-nav__link::after {
      content: "";
  }
  .main-nav__item.menu-item-has-children:has(.focus-visible) .main-nav__link svg {
      color: #182c8f;
      -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
      transform: rotate(180deg);
  }
  .main-nav__item.menu-item-has-children:has(.focus-visible) .main-nav__sublist {
      opacity: 1;
      visibility: visible;
  }
  .main-nav__item.menu-item-has-children.focus-visible:focus .main-nav__link {
      color: #182c8f;
  }
  .main-nav__item.menu-item-has-children.focus-visible:focus .main-nav__link::after {
      content: "";
  }
  .main-nav__item.menu-item-has-children.focus-visible:focus .main-nav__link svg {
      color: #182c8f;
      -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
      transform: rotate(180deg);
  }
  .main-nav__item.menu-item-has-children.focus-visible:focus .main-nav__sublist {
      opacity: 1;
      visibility: visible;
  }
}


@media (min-width: 1023px) and (hover: hover) {
  .main-nav__item.menu-item-has-children:hover .main-nav__link {
      color: #182c8f;
  }
  .main-nav__item.menu-item-has-children:hover:not(.focus-visible) .main-nav__link::after {
      content: "";
  }
  .main-nav__item.menu-item-has-children:hover:not(.focus-visible) .main-nav__link svg {
      color: #182c8f;
      -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
      transform: rotate(180deg);
  }
  .main-nav__item.menu-item-has-children:hover:not(.focus-visible) .main-nav__sublist {
      opacity: 1;
      visibility: visible;
  }
}



